
// ==============================================

//        HEADER

// ==============================================


//  Header
// -----------------------------------------------

.header {
  background-color: white;

  .wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 110px;
  }

  .logo-link {
    display: inline-block;

    svg.logo-desktop {
      width: 86px;
      height: 99px;
    }
    svg.logo-mobile {
      display: none;
      margin-left: 1rem;
      width: 181px;
      height: 30px;
    }
  }

  .box-don--desktop {
    position: absolute;
    right: 1rem;
    top: 25px;
    z-index: 1;
  }

  .btn-donate--mobile {
    display: none;
    position: absolute;
    right: 1rem;
    top: 13px;
    transform-origin: top right;
    transform: scale(0.6);
  }
}



//  MQ
// -----------------------------------------------

@include breakpoint(small down) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    z-index: 10;
    border-bottom: 1px solid #eeeeee;

    .wrapper {
      justify-content: left;
      height: 60px;
    }
    .logo-link svg.logo-desktop {
      display: none;
    }
    .logo-link svg.logo-mobile {
      display: block;
    }
    .btn-donate--mobile {
      display: inline-flex;
    }
    .box-don--desktop {
      display: none;
    }
  }
}
