//  Nav mobile inside slide
// -----------------------------------------------

.itw-slide__btn-accordion {
  @extend .nav-itw__link;
  display: none;
  position: relative;
  height: 120px;
  font-size: 30px;
  background-color: white;
  
  &:not([href="#itw-slide3"]):after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    height: 2px;
    width: 90%;
    transform: translateX(-50%);
    background-color: #DECCCA;
  }
}


@include breakpoint(small only) {
  .nav-itw {
    display: none;
  }
  .itw-slide__btn-accordion {
    display: flex;
  }
}

